@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;600;700;800&display=swap);
*{
  margin: 0;
  padding: 0;
  border:0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
}

body {
  background-color: #15202B;
  height: 100%;
}

.wrapper{
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.container{
  max-width: 760px;
  padding: 0 15px;
  margin: 0 auto;
}

.posts{
  padding: 100px 0 30px 0;
}

.title{
  margin-bottom: 40px;
  font-size: 2rem;
  font-weight: 600;
  color: rgba(255,255,255,0.8);
}
.post{
    max-width: 100%;
    display: flex;
    margin-bottom: 20px;
}

.post__avatar-image{
    display: block;
    max-width: 50px;
    height: auto;
    object-fit: cover;
    border-radius: 50%;
}

.post__inner{
    padding-left: 15px;
    display: flex;
    flex-direction: column;
}

.post__header{
    display: flex;
    justify-content: space-between;
}

.post__info{
    display: flex;
    align-items: center;
    justify-self: flex-start;
}

.post__info span{
    margin-right: 10px;
    line-height: 1.2;
}

.author-name{
    color:#fff;
    font-weight: 700;
}

.author-nickname{
    position: relative;
    margin-right: 15px !important;
}

.author-nickname::before{
    content: '';
    width: 2px;
    height: 2px;
    background-color:  rgba(255,255,255,0.7);
    border-radius: 50%; 
    top: 50%;
    right: -8px;
    position: absolute;
}

.author-nickname, .post__date{
    color: rgba(255,255,255,0.7);
}

.post__body{
    display: flex;
    flex-direction: column;
}

.post__text{
    margin-top: 10px;
    line-height: 1.2;
    color:rgba(255,255,255,0.8);
}

.post__image{
    margin-top: 10px;
    display: block;
    max-width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 10px;
}

.svg-icon{
    width: 16px;
    height: auto;
}

.svg-icon.approved{
    margin-left: 5px;
}

.post__button{
    color:rgba(255,255,255,0.7);
    font-size: 1.2rem;
    background: transparent;
    cursor: pointer;
}

.post__button--active{
    color: #FF4D4D;
    transition: 0.3s all ease-in-out;
}

.post__footer{
    padding-top: 15px;
    display: flex;
    flex-wrap: wrap;
}

.post__column-4{
    flex: 1 0 25%;
}

.action-info{
    margin-left: 10px;
    line-height: 1.2;
    color:rgba(255,255,255,0.7);
}

.action-info--active{
    color: #FF4D4D;
    transition: 0.3s all ease-in-out; 
}
.add-post-form{
    padding-top: 30px;
    width: 100%;
}

.form-title{
    margin-bottom: 20px;
    color: rgba(255,255,255, 0.8);
    font-size: 1.5rem;
}

.form-group{
    margin-bottom: 15px;
    width: 100%;
}

.form-label{
    font-size: 1rem;
    color: rgba(255,255,255, 0.8);
    font-weight: 400;
    line-height: 1.5;
    margin-bottom: 10px;
    display: block;
}

.form-input{
    width: 100%;
    border: 2px solid rgba(255,255,255,0.8);
    border-radius: 10px;
    background-color: transparent;
    padding: 10px 20px;
    font-size: 1rem;
    color: rgba(255,255,255,0.8);
}

.form-input::-webkit-input-placeholder{
    color: rgba(255,255,255,0.5);
}

.form-input:-ms-input-placeholder{
    color: rgba(255,255,255,0.5);
}

.form-input::placeholder{
    color: rgba(255,255,255,0.5);
}

.form-input:focus{
    outline: none;
}

.form-textarea{
    resize: vertical;
    width: 100%;
    overflow: auto;
}

.form-option{
    background: #15202B;
}

.form-btn{
    min-width: 150px;
    margin-top: 10px;
    padding: 10px 20px;
    border: 1px solid rgba(255,255,255,0.8);
    border-radius: 10px;
    background-color: #3A5977;;
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    cursor: pointer;
}
